<template>
  <q-card flat bordered class="rounded">
    <q-card-section class="card-padding-title-xs">
      <q-toolbar class="q-pa-none">
        <q-toolbar-title class="text-h5 text-primary-700 font-medium">
          Users Information
        </q-toolbar-title>

        <div
          v-if="orgUsers.length && (isOrgOwner() || isOrgAdmin())"
          class="q-gutter-sm"
        >
          <DynamicButton
            iconSize="xs"
            label="Invite User"
            icon="icon-user-plus-01"
            :alwaysShowIcon="true"
            @click="inviteUser"
          />
        </div>
      </q-toolbar>
    </q-card-section>

    <q-separator />

    <q-table
      row-key="id"
      filteredSortedRows
      binary-state-sort
      :rows="orgUsers"
      :columns="columns"
      color="primary"
      :loading="tableLoading"
      rows-per-page-label="Users per page"
      no-data-label="No users found."
      no-results-label="The filter didn't uncover any results."
      loading-label="Getting data from the server."
      :rows-per-page-options="[10, 20, 30, 50, 50, 0]"
      style="max-height: 1080px"
      :visible-columns="visibleColumns"
    >
      <!-- Loading -->
      <template v-slot:loading>
        <div class="bg-color absolute-top" style="top: 43px">
          <q-linear-progress size="2px" indeterminate color="secondary" />
        </div>
      </template>

      <!-- Table Header Slot -->
      <template v-slot:header="props">
        <q-tr class="bg-color">
          <q-th
            auto-width
            :props="props"
            :key="col.name"
            v-for="(col, i) in props.cols"
            style="position: sticky; top: 0; z-index: 2"
            :style="i === 0 ? 'padding-left: 24px' : ''"
          >
            <span class="text-body2 font-medium text-primary-500">
              {{ col.label }}
            </span>
          </q-th>
          <q-th
            v-if="isOrgOwner() || isOrgAdmin()"
            auto-width
            style="position: sticky; top: 0; z-index: 2; padding-right; 24px"
            class="text-right"
          >
            <!-- User Options -->
          </q-th>
        </q-tr>
      </template>

      <!-- Table Body Slot -->
      <template v-slot:body="props">
        <q-tr :props="props" class="hover-bg-accent">
          <q-td
            v-for="(_r, i) in props.cols"
            :key="_r.name || i"
            :props="props"
            class="cursor-pointer text-center text-primary q-py-md"
            :style="
              i === 0
                ? 'padding-left: 24px; padding-top: 18px; padding-bottom: 18px'
                : ''
            "
          >
            <div class="row items-center q-col-gutter-sm">
              <span class="text-body2" :class="i === 0 ? 'font-medium' : ''">
                {{ _r.value }}
              </span>
              <q-icon
                v-if="
                  i === 1 &&
                  selectedOrgUsers?.[props.row?.id]?.status === 'pending'
                "
                size="xs"
                color="warning"
                name="icon-clock"
              >
                <tooltip-pro>Invitation Pending</tooltip-pro>
              </q-icon>
            </div>
          </q-td>
          <q-td
            v-if="isOrgOwner() || isOrgAdmin()"
            style="padding-right: 24px; padding-top: 18px; padding-bottom: 18px"
          >
            <div class="q-gutter-sm text-right">
              <q-btn
                :disable="isOrgOwner(props.row?.id)"
                flat
                round
                size="sm"
                color="primary-500"
                @click="deleteUser(props.row?.id)"
                icon="icon-trash-01"
                :class="{ ['hover-text-negative']: !isOrgOwner(props.row?.id) }"
              />

              <q-btn
                :disable="isOrgOwner(props.row?.id)"
                flat
                round
                size="sm"
                color="primary-500"
                @click="editUser(props.row?.id)"
                icon="icon-edit-01"
                :class="{
                  ['hover-text-secondary']: !isOrgOwner(props.row?.id),
                }"
              />
            </div>
          </q-td>
        </q-tr>
      </template>

      <!-- Bottom -->
      <template v-slot:bottom="scope">
        <div
          v-if="$q.screen.lt.xs || $q.screen.xs"
          class="q-py-md row full-width items-center justify-center q-col-gutter-sm q-virtual-scroll--skip"
        >
          <!-- Pagination -->
          <q-pagination
            flat
            unelevated
            boundary-numbers
            v-model="scope.pagination.page"
            :disable="tableLoading"
            color="transparent"
            text-color="primary-500"
            active-color="primary-50"
            active-text-color="primary-700"
            :max="scope.pagesNumber"
            :max-pages="6"
          />
        </div>
        <div
          class="q-py-md row full-width items-center justify-between q-col-gutter-sm q-virtual-scroll--skip"
        >
          <!-- Previous Page -->
          <div class="text-left" :class="$q.screen.gt.xs ? 'col-2' : 'col-6'">
            <q-btn
              dense
              outline
              no-caps
              :disable="scope.isFirstPage"
              @click="scope.prevPage"
              class="rounded q-px-md slide-hover button-gm-outline-dark text-body1 font-medium button-sm"
              :class="{
                [scope.isFirstPage ? 'text-primary-500' : 'text-primary']: true,
                ['hover-text-secondary']: !scope.isFirstPage,
              }"
            >
              <q-icon size="xs" name="icon-arrow-narrow-left" class="q-pr-xs" />
              Previous
            </q-btn>
          </div>

          <!-- Pagination -->
          <q-pagination
            v-if="$q.screen.gt.xs"
            flat
            unelevated
            boundary-numbers
            v-model="scope.pagination.page"
            :disable="tableLoading"
            color="transparent"
            text-color="primary-500"
            active-color="primary-50"
            active-text-color="primary-700"
            :max="scope.pagesNumber"
            :max-pages="6"
          />

          <!-- Next Page -->
          <div class="text-right" :class="$q.screen.gt.xs ? 'col-2' : 'col-6'">
            <q-btn
              dense
              outline
              no-caps
              :disable="scope.isLastPage"
              @click="scope.nextPage"
              class="rounded q-px-md slide-hover button-gm-outline-dark text-body1 font-medium button-sm"
              :class="{
                [scope.isLastPage ? 'text-primary-500' : 'text-primary']: true,
                ['hover-text-secondary']: !scope.isLastPage,
              }"
            >
              Next
              <q-icon
                size="xs"
                name="icon-arrow-narrow-right"
                class="q-pl-xs"
              />
            </q-btn>
          </div>
        </div>
      </template>

      <!-- No Data -->
      <template v-slot:no-data="{ icon, message }">
        <div class="full-width text-center">
          <div class="full-width row flex-center text-primary-500">
            <q-icon
              v-if="tableLoading"
              class="q-pr-sm"
              size="sm"
              :color="tableLoading ? 'secondary' : 'negative'"
              :name="tableLoading ? 'icon-download-cloud-01' : icon"
            />
            <span class="text-body2 text-primary-500 font-medium">
              {{ message }}
            </span>
          </div>
          <div
            v-if="!tableLoading && (isOrgOwner() || isOrgAdmin())"
            class="full-width row flex-center text-primary-500 q-pt-sm"
          >
            <q-btn
              no-caps
              unelevated
              class="button-sm"
              @click="inviteUser"
              color="primary"
            >
              <q-icon name="icon-user-plus-01" size="20px" />
              <span class="q-pl-xs">Invite User</span>
            </q-btn>
          </div>
        </div>
      </template>
    </q-table>

    <q-dialog
      v-model="dialog_AddEditUser"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <Add_Edit_User
        :uid="userToEdit"
        style="max-width: 80vw; width: 800px"
        @added="dialog_AddEditUser = false"
        @updated="dialog_AddEditUser = false"
        @close="dialog_AddEditUser = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import Add_Edit_User from 'src/components/Users/Add_Edit_User.vue';
import { extractErrorMessageString } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'UsersList',

  components: { DynamicButton, Add_Edit_User },

  data() {
    return {
      rows: [],
      visibleColumns: [],
      userToEdit: 0,
      tableLoading: true,
      dialog_AddEditUser: false,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrgUsers']),
    ...mapState(useGrowmodo, ['userInvitedRoles']),

    orgUsers() {
      return Object.values(this.selectedOrgUsers).filter(
        (e) => e.id !== this.user.id
      );
    },

    columns() {
      return [
        {
          name: 'id',
          required: false,
          label: 'Id',
          align: 'left',
          field: (row) => `${row.id}`,
          format: (val) => val,
          sortable: true,
        },
        {
          name: 'fullname',
          required: true,
          label: 'User',
          align: 'left',
          field: (row) => `${row.firstname} ${row.lastname}`,
          format: (val) => val,
          sortable: true,
        },
        {
          name: 'email',
          align: 'left',
          label: 'Email Address',
          field: (row) => row.email,
          format: (val) => val,
          sortable: true,
        },
        {
          name: 'role',
          align: 'left',
          label: 'User Role',
          field: 'role',
          field: (row) => row.id,
          format: (val) =>
            this.isOrgOwner(val)
              ? 'Owner'
              : this.userInvitedRoles[this.getUserRole(val)]?.label,
          sortable: true,
        },
      ];
    },
  },

  async mounted() {
    this.getOrgUsers().finally(() => {
      this.tableLoading = false;
    });
    this.visibleColumns = this.columns
      .map((c) => c.name) // Get all the column names
      .filter((e) => !['id'].includes(e)); // Hide some columns initially
  },

  methods: {
    ...mapActions(useUser, [
      'getOrgUsers',
      'deleteOrgUser',
      'isOrgOwner',
      'isOrgAdmin',
      'getUserRole',
    ]),

    inviteUser() {
      this.userToEdit = 0;
      this.dialog_AddEditUser = true;
    },

    async deleteUser(userId) {
      this.tableLoading = true;
      const response = await this.deleteOrgUser(userId);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: message || 'User has been removed from your organization',
        });
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'An error occured while removing the user',
        });
      }
      this.tableLoading = false;
    },

    editUser(userId) {
      this.userToEdit = userId;
      this.dialog_AddEditUser = true;
    },
  },
});
</script>
