<template>
  <q-card flat bordered class="rounded">
    <q-card-section class="card-padding-title-xs">
      <q-toolbar class="q-pa-none">
        <q-toolbar-title class="text-h5 text-primary-700 font-medium">
          Company Information
        </q-toolbar-title>

        <div v-if="isOrgOwner() || isOrgAdmin()" class="q-gutter-sm">
          <DynamicButton
            v-if="!editing"
            outline
            alwaysShowIcon
            iconSize="xs"
            label="Edit Info"
            icon="icon-pencil-02"
            @click="editing = true"
            class="button-gm-outline-dark"
          />

          <DynamicButton
            v-if="editing"
            outline
            :flat="$q.screen.lt.xs || $q.screen.xs"
            iconSize="xs"
            label="Cancel"
            icon="icon-x-close"
            class="button-gm-outline-dark"
            @click="cancelEditing"
          />

          <DynamicButton
            v-if="editing"
            alwaysShowIcon
            iconSize="xs"
            label="Save Changes"
            icon="icon-save-01"
            @click="saveOrgInfo"
          />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-separator />
    <q-card-section
      class="q-col-gutter-lg text-body1 card-padding"
      :style="editing ? 'max-width: 900px' : ''"
    >
      <div
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          Company Name<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            v-if="editing"
            outlined
            placeholder="Name"
            v-model="company.title"
            @change="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            {{ selectedOrg.title }}
          </div>
        </div>
      </div>
      <div
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          Company Type<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <SelectCompanyType
            v-if="editing"
            v-model="company.company_type_id"
            :initModel="company.company_type_id"
            @update:model-value="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            {{ companyTypes[selectedOrg.company_type_id]?.label }}
          </div>
        </div>
      </div>
      <div
        v-if="0 && (editing || selectedOrg.about_us)"
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          About Us<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            v-if="editing"
            outlined
            type="textarea"
            placeholder="About the company..."
            v-model="company.about_us"
            @change="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            {{ selectedOrg.about_us }}
          </div>
        </div>
      </div>
      <div
        v-if="editing || selectedOrg.website"
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          Website
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputWebsite
            v-if="editing"
            :rule="undefined"
            v-model="company.website"
            @change="changes_made = true"
          />
          <div v-else class="letter-space-normal">
            <external-link :url="selectedOrg.website" />
          </div>
        </div>
      </div>
      <div
        v-if="
          editing ||
          selectedOrg.address_line_1 ||
          selectedOrg.city ||
          selectedOrg.state ||
          selectedOrg.country
        "
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium font-medium"
          :class="{
            ['self-start']: false,
            [`text-primary-${editing ? '800' : '500'}`]: true,
          }"
        >
          Country<span v-if="editing" class="text-red">*</span>
        </div>
        <div v-if="editing" class="col-12 col-sm-8 col-md-9">
          <div class="row q-col-gutter-md items-center justify-start">
            <template v-if="0">
              <div class="col-12">
                <q-input
                  outlined
                  placeholder="Street Name"
                  v-model="company.address_line_1"
                  @change="changes_made = true"
                />
              </div>
              <div class="col-12">
                <q-input
                  outlined
                  placeholder="City"
                  v-model="company.city"
                  @change="changes_made = true"
                />
              </div>
              <div class="col-12 col-md-6">
                <q-input
                  outlined
                  placeholder="State"
                  v-model="company.state"
                  @change="changes_made = true"
                />
              </div>
              <div class="col-12 col-md-6">
                <q-input
                  outlined
                  placeholder="ZIP Code"
                  v-model="company.zipcode"
                  @change="changes_made = true"
                />
              </div>
            </template>
            <div class="col-12">
              <SelectCountry
                v-model="company.country"
                :initModel="company.country"
                @update:model-value="changes_made = true"
              />
            </div>
          </div>
        </div>
        <div
          v-else
          class="col-12 col-sm-8 col-md-9 q-gutter-sm letter-space-normal"
        >
          <template v-if="0">
            <div>
              {{ selectedOrg.address_line_1 }}
            </div>
            <div>
              {{ selectedOrg.firstName }}
            </div>
            <div
              v-if="
                selectedOrg.city || selectedOrg.state || selectedOrg.zipcode
              "
            >
              {{ selectedOrg.city }}
              {{ selectedOrg.state ? ', ' + selectedOrg.state : '' }}
              {{ selectedOrg.zipcode ? ', ' + selectedOrg.zipcode : '' }}
            </div>
          </template>
          <div>
            {{ selectedOrg.country }}
          </div>
        </div>
      </div>
      <div
        v-if="
          0 &&
          ((selectedOrg.phone_country_code && selectedOrg.phone_number) ||
            editing)
        "
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          Contact Number<span v-if="editing" class="text-red"></span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <InputPhoneNumber
            v-if="editing"
            @areaCodeChanged="
              (e) => {
                company.phone_country_code = e;
                if (e != selectedOrg.phone_country_code) changes_made = true;
              }
            "
            @numberChanged="
              (e) => {
                company.phone_number = e;
                if (e != selectedOrg.phone_number) changes_made = true;
              }
            "
            :areaCode="company.phone_country_code"
            :phoneNumber="company.phone_number"
          />
          <div v-else class="letter-space-normal">
            <external-link
              labelClass="text-primary"
              :url="`tel:+${selectedOrg.phone_country_code}${selectedOrg.phone_number}`"
              :label="`+${selectedOrg.phone_country_code} ${selectedOrg.phone_number}`"
            />
          </div>
        </div>
      </div>
      <div
        v-if="editing || selectedOrg.employee_count_id"
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="col-12 col-sm-4 col-md-3 font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          Employees<span v-if="editing" class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9 letter-space-normal">
          <SelectEmployeeCount
            v-if="editing"
            v-model="company.employee_count_id"
            :initModel="company.employee_count_id"
            @updated="changes_made = true"
          />
          <template v-else>
            {{ employeeCount[selectedOrg.employee_count_id]?.label || '*' }}
          </template>
        </div>
      </div>
      <div
        v-if="
          0 &&
          (editing || Object.values(selectedOrg.social_accounts || {})?.length)
        "
        class="row full-wdith items-center justify-start"
        :class="{ ['q-col-gutter-sm']: editing }"
      >
        <div
          class="self-start col-12 col-sm-4 col-md-3 font-medium"
          :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
        >
          Social Media Links
        </div>
        <div class="col-12 col-sm-8 col-md-9 letter-space-normal">
          <div v-if="editing" class="q-gutter-md">
            <template v-for="opt in socialMedia" :key="opt.platform">
              <q-input
                outlined
                placeholder="company_username"
                v-model="company.social_accounts[opt.platform]"
                @change="changes_made = true"
              >
                <template v-slot:prepend>
                  <div
                    class="text-body2"
                    :class="{
                      [`text-primary-${editing ? '800' : '500'}`]: true,
                    }"
                  >
                    {{
                      (getSocialInfo(opt.platform)?.base_url || '')
                        .replace('http://', '')
                        .replace('https://', '')
                        .replace('www.', '')
                    }}{{ !opt.platform.endsWith('/') ? '/' : '' }}
                  </div>
                  <q-separator vertical class="q-ml-sm" />
                </template>
              </q-input>
            </template>
          </div>
          <div v-else class="q-gutter-sm letter-space-normal">
            <div
              v-for="opt in Object.keys(selectedOrg.social_accounts)"
              :key="opt"
            >
              <external-link
                :url="
                  (getSocialInfo(opt)?.base_url || '') +
                  '/' +
                  selectedOrg.social_accounts[opt]
                "
                :icon="formatMediaPlatformIconSrc(opt)"
              />
            </div>
          </div>
        </div>
      </div>
    </q-card-section>

    <!-- Dialog Unsaved Edit -->
    <q-dialog v-model="unsaved">
      <dynamic-dialog-confirmation
        v-bind="$root.messagesDialogs?.editingUnsaved"
        @okay="editing = false"
      />
    </q-dialog>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import {
  extractErrorMessageString,
  objectCopy,
  url2link,
} from 'src/assets/scripts/functions';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputWebsite from 'src/components/Helpers/InputWebsite.vue';
import InputPhoneNumber from 'src/components/Helpers/InputPhoneNumber.vue';
import SelectCompanyType from 'src/components/Helpers/SelectCompanyType.vue';
import SelectEmployeeCount from 'src/components/Helpers/SelectEmployeeCount.vue';
import { mapActions, mapState } from 'pinia';
import useHelpers from 'stores/helpers';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import SelectCountry from 'src/components/Helpers/SelectCountry.vue';

export default defineComponent({
  name: 'CompanyProfile',

  components: {
    DynamicButton,
    InputPhoneNumber,
    SelectCompanyType,
    InputWebsite,
    SelectCountry,
    SelectEmployeeCount,
  },

  data() {
    return {
      url2link: url2link,
      editing: false,
      unsaved: false,
      company: {},
      changes_made: false,
    };
  },

  computed: {
    ...mapState(useHelpers, ['socialMedia', 'employeeCount']),
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, ['companyTypes']),
  },

  watch: {
    editing: {
      async handler(val) {
        if (val) {
          // Editing
          // Update the editing value
          this.company = objectCopy(this.selectedOrg);
        } else {
          // Viewing
          // Reset company info
          this.company = {};
          this.changes_made = false;
        }
      },
    },
  },

  mounted() {
    this.getEmployeeCount();
  },

  methods: {
    ...mapActions(useUser, ['updateOrganization', 'isOrgOwner', 'isOrgAdmin']),
    ...mapActions(useHelpers, [
      'getSocialInfo',
      'formatMediaPlatformIconSrc',
      'getEmployeeCount',
    ]),

    async saveOrgInfo() {
      if (!this.changes_made) {
        this.editing = false;
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'No changes has been made',
        });
        return;
      }
      const _ = this.company || {};
      const payload = {
        title: _.title,
        address_line_1: _.address_line_1,
        state: _.state,
        company_type_id: _.company_type_id,
        about_us: _.about_us,
        city: _.city,
        zipcode: _.zipcode,
        country: _.country,
        phone_number: _.phone_number,
        phone_country_code: _.phone_country_code,
        employee_count_id: _.employee_count_id,
        social_accounts: {},
      };
      for (const k of Object.keys(_.social_accounts)) {
        if (_.social_accounts[k])
          payload.social_accounts[k] = _.social_accounts[k];
      }
      if (_.address_line_2) payload.address_line_2 = _.address_line_2;
      if (_.website) payload.website = _.website;
      const response = await this.updateOrganization(payload);
      if (response.success) {
        this.editing = false;
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your changes have been successfully saved',
        });
      } else {
        const message = extractErrorMessageString(response?.message);
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error updating organization',
        });
      }
    },

    cancelEditing() {
      if (this.changes_made) {
        this.unsaved = true;
      } else {
        this.editing = false;
      }
    },
  },
});
</script>
