<template>
  <q-page padding>
    <q-layout container view="LHh lpR lFf" class="absolute-full bg-color">
      <q-drawer
        dark
        bordered
        :show-if-above="$q.screen.gt.sm"
        side="left"
        class="drawer-settings"
        :class="{
          ['drawer-settings-dark']: $root.dark,
          ['bg-white']: !$root.dark,
        }"
        :width="250"
        :breakpoint="600"
        v-model="$root.settingsDrawer"
      >
        <q-scroll-area
          class="fit"
          :horizontal-thumb-style="{ opacity: 0 }"
          :vertical-thumb-style="{ opacity: 0 }"
        >
          <q-list padding class="q-pt-lg">
            <q-item
              class="text-body1 font-medium padding-4b q-mx-sm"
              :class="{
                ['text-primary-50']: $root.dark,
                ['text-primary']: !$root.dark,
              }"
            >
              <q-item-section>Settings</q-item-section>
            </q-item>

            <template v-for="(item, i) in settingsMenu" :key="i">
              <q-item
                v-if="item.view !== false"
                clickable
                v-ripple
                class="menu-item text-body1 rounded font-medium q-mx-sm"
                :to="
                  item.noswitch !== true ? '/settings/' + item.key : undefined
                "
                @click="
                  () => {
                    // Switch
                    if (item.noswitch !== true) {
                      tab = item.key;
                    }
                    // Perform Action
                    if (item.action) item.action();
                  }
                "
              >
                <q-item-section avatar class="q-pr-sm" style="min-width: unset">
                  <q-avatar rounded size="sm" class="rounded">
                    <q-icon :name="item.icon" size="sm" />
                  </q-avatar>
                </q-item-section>
                <q-item-section>{{ item.label }}</q-item-section>
                <q-item-section
                  v-if="item.badge"
                  avatar
                  style="min-width: unset"
                >
                  <q-badge
                    rounded
                    :color="item.badge?.color"
                    :class="
                      item.badge?.class +
                      ' ' +
                      (tab === item.key
                        ? item.badge?.active_class
                        : item.badge?.inactive_class)
                    "
                  >
                    {{ item.badge?.message }}
                  </q-badge>
                </q-item-section>
              </q-item>
            </template>
          </q-list>
        </q-scroll-area>
      </q-drawer>

      <q-page-container>
        <q-page class="container">
          <q-tab-panels
            v-model="tab"
            animated
            transition-prev="fade"
            transition-next="fade"
            class="bg-transparent"
          >
            <q-tab-panel name="profile">
              <AccountProfile />
            </q-tab-panel>

            <q-tab-panel name="company_profile">
              <CompanyProfile />
            </q-tab-panel>

            <q-tab-panel name="users">
              <UsersList />
            </q-tab-panel>

            <q-tab-panel name="subscription">
              <SubscriptionInformation />
            </q-tab-panel>
          </q-tab-panels>

          <!-- Dialog Confirm Logout -->
          <q-dialog v-model="confirmLogout">
            <dynamic-dialog-confirmation
              v-bind="$root.messagesDialogs?.confirmLogout"
              @okay="logoutAccount()"
            />
          </q-dialog>
        </q-page>
      </q-page-container>
    </q-layout>
  </q-page>
</template>

<script>
import { defineComponent } from 'vue';
import AccountProfile from 'src/components/Accounts/Profile.vue';
import CompanyProfile from 'src/components/Accounts/Company.vue';
import SubscriptionInformation from 'src/components/Accounts/Subscription.vue';
import UsersList from 'src/components/Users/UsersList.vue';
import { mapActions, mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useUser from 'stores/user';

export default defineComponent({
  name: 'AccountSettingsPage',

  components: {
    AccountProfile,
    CompanyProfile,
    SubscriptionInformation,
    UsersList,
  },

  data() {
    const settingsMenu = {
      profile: {
        key: 'profile',
        label: 'Profile',
        icon: 'icon-user-03',
      },
      company_profile: {
        key: 'company_profile',
        label: 'Company',
        icon: 'icon-building-05',
      },
      users: {
        key: 'users',
        label: 'Users',
        icon: 'icon-users-02',
      },
      subscription: {
        key: 'subscription',
        label: 'Subscription',
        icon: 'icon-wallet-02',
      },
      billing: {
        key: 'billing',
        noswitch: true,
        label: 'Billing',
        icon: 'icon-credit-card-02',
        view: this.isOrgOwner() || this.isOrgAdmin() || this.isOrgBiller(),
        action: this.goToBilling,
      },
      logout: {
        key: 'logout',
        noswitch: true,
        label: 'Logout',
        icon: 'icon-log-out-01',
        action: () => {
          this.confirmLogout = true;
        },
      },
    };
    return {
      title: 'Account Settings',
      tab: '',
      settingsMenu,
      editing: false,
      confirmLogout: false,
    };
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    this.$root.toggleSettingsDrawer(true);
    this.tab = this.settingsMenu.profile?.key;
    if (this.$q.screen.gt.sm) {
      if (this.routeTab) {
        let menu = this.settingsMenu[this.routeTab]?.key;
        if (menu) this.tab = this.routeTab;
      } else {
        this.$router.replace(`/settings/${this.settingsMenu.profile?.key}`);
      }
    }
  },

  watch: {
    tab: {
      async handler(val) {
        this.$root.setMeta({
          title: this.settingsMenu?.[val]?.label || 'Settings',
        });
      },
    },
    routeTab: {
      async handler(val) {
        if (this.$q.screen.gt.sm) {
          if (this.$route.path == '/settings') {
            this.$router.replace(
              `/settings/${val || this.settingsMenu.profile?.key}`
            );
            this.tab = val || this.settingsMenu.profile?.key;
          }
        }
      },
    },
  },

  computed: {
    ...mapState(useGrowmodo, ['redirect']),

    routeTab() {
      return this.$route.params?.settings_tab;
    },
  },

  methods: {
    ...mapActions(useUser, [
      'logout',
      'isOrgOwner',
      'isOrgAdmin',
      'isOrgBiller',
    ]),

    async logoutAccount() {
      const response = await this.logout();
      if (!response.success) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'An error occured during logout',
        });
      }
    },

    goToBilling() {
      this.$root.toggleRedirectConfirmation(this.redirect?.stripe);
    },
  },

  beforeUnmount() {
    this.$root.toggleSettingsDrawer(false);
  },
});
</script>
