<script>
import { defineComponent } from 'vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import { mapState, mapActions } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import useHelpers from 'stores/helpers';
import {
  formatMoney,
  extractErrorMessageString,
} from 'src/assets/scripts/functions.js';

export default defineComponent({
  components: { CardIconBox },
  name: 'OffBoarding',

  emits: ['start', 'done', 'exit', 'stepUpdated'],

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
    setStep: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      offboard_submitting: false,
      offboarded: false,
      formStep: '',
      pause: {
        duration: null,
      },
      cancel: {
        reason: null,
        otherReason: null,
        q1: false,
        q2: false,
        q3: false,
      },
      formatMoney,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgIsMaintenance']),
    ...mapState(useGrowmodo, ['maintenance']),
  },

  watch: {
    formStep: {
      async handler(val) {
        this.$emit('stepUpdated', val);
      },
    },
    setStep: {
      async handler(val) {
        this.formStep = val || 'init';
      },
    },
  },

  mounted() {
    this.formStep = this.setStep || 'init';
  },

  methods: {
    ...mapActions(useUser, [
      'logout',
      'switchToMaintenancePlan',
      'pauseSubscription',
      'cancelSubscription',
    ]),
    ...mapActions(useGrowmodo, ['planPriceCalculator']),
    ...mapActions(useHelpers, ['getCurrency']),

    start(payload) {
      this.$emit('start', payload);
      if (this.formStep < 2) this.formStep++;
    },

    prevButtonClicked() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextButtonClicked() {
      this.$refs?.['formStepper']?.next?.();
    },

    submitFormAction() {
      // Initialize
      // Done
      this.nextButtonClicked();
      this.offboarded = true;
      // Safe reset
      setTimeout(() => {}, 1500);
    },

    backToHome() {
      this.$router.push('/');
      this.$emit('exit');
    },

    async switchToMaintenance() {
      this.offboard_submitting = true;
      const response = await this.switchToMaintenancePlan();
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.formStep = 'maintenance_done';
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error while processing your request',
        });
      }
      this.offboard_submitting = false;
    },

    async pauseAccount() {
      const response = await this.pauseSubscription({
        pause_duration: this.pause.duration,
      });
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.formStep = 'pause_done';
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error while processing your request',
        });
      }
    },

    async cancelAccount() {
      let emsg = '';
      switch (false) {
        case !!this.cancel?.reason:
          emsg = 'Reason for cancellation is not provided';
          break;
        case !!this.cancel?.q1 && !!this.cancel?.q2 && !!this.cancel?.q3:
          emsg = 'Please confirm your account cancellation';
          break;
        default:
          break;
      }
      if (emsg) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: emsg,
        });
      } else {
        const reason =
          this.cancel.reason === 'Others'
            ? `Other Reason - ${this.cancel?.otherReason || 'Unknown'}`
            : this.cancel?.reason;
        const response = await this.cancelSubscription({
          answer: reason,
        });
        const message = extractErrorMessageString(response?.message);
        if (response.success) {
          if (process.env.DEV_MODE) console.log('Account canceled.');
          this.formStep = 'cancel_done';
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: 'Account has been canceled',
          });
        } else {
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: message || 'Unexpected error has occured',
          });
        }
      }
    },

    requestMaintenanceTask() {
      if (process.env.DEV_MODE) console.log('Maintenance Task Requested');
      this.$root.toggleRequest?.('task');
      this.$emit('exit');
    },

    stopCancelProcess() {
      if (process.env.DEV_MODE) console.log('Changed of mind.');
      this.$emit('exit');
    },
  },

  beforeUnmount() {
    this.$emit('exit');
  },
});
</script>
