<template>
  <q-select
    :autofocus="autofocus"
    :dense="dense"
    outlined
    emit-value
    hide-bottom-space
    option-value="value"
    option-label="label"
    class="rounded text-body2 input-white text-primary"
    popup-content-class="bg-white rounded-bottom"
    input-class="text-body1 letter-space-normal text-primary"
    :options="listOptions"
    v-model="selectModel"
    :rules="[(val) => (required ? !!val || '' : true)]"
  >
    <template v-slot:option="scope">
      <q-item
        clickable
        v-bind="scope.itemProps"
        :class="scope.selected ? 'bg-accent text-secondary' : 'text-primary'"
        class="hover-text-secondary hover-bg-accent"
        @click="scope.opt?.action"
      >
        <q-item-section avatar v-if="scope.opt?.icon" style="min-width: unset">
          <q-icon size="xs" :name="scope.opt?.icon" />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{ scope.opt?.label || scope.opt }}
          </q-item-label>
        </q-item-section>
      </q-item>
    </template>

    <template v-slot:selected>
      <span :class="selectModel ? '' : 'text-placeholder'">{{
        userInvitedRoles[selectModel]?.label || placeholder
      }}</span>
    </template>
  </q-select>
</template>

<script>
import { defineComponent } from 'vue';
import { mapState } from 'pinia';
import useGrowmodo from 'stores/growmodo';

export default defineComponent({
  name: 'SelectRoles',

  emits: ['updated'],

  props: {
    autofocus: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: 'Select role',
    },
    initModel: {
      type: [String, Number],
      default: '',
    },
    dense: {
      type: Boolean,
      default: true,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      selectModel: '',
    };
  },

  watch: {
    initModel: {
      async handler(val) {
        if (val) this.selectModel = val;
      },
    },
    selectModel: {
      async handler(val) {
        if (val !== this.initModel) this.$emit('updated', val);
      },
    },
  },

  mounted() {
    if (this.initModel) this.selectModel = this.initModel;
  },

  computed: {
    ...mapState(useGrowmodo, ['userInvitedRoles']),

    listOptions() {
      return Object.values(this.userInvitedRoles);
    },
  },
});
</script>
