<template>
  <q-card flat>
    <div v-if="planTypes" class="full-width text-center q-pb-lg no-wrap">
      <q-btn-group
        :spread="$q.screen.sm || $q.screen.lt.md"
        class="rounded no-shadow border-add"
      >
        <template
          v-for="(planType, i) in Object.values(subscription_BillingTypes)"
          :key="planType"
        >
          <q-btn
            flat
            no-caps
            class="hover-text-secondary"
            :color="selectedBilling === planType.id ? 'secondary' : 'primary'"
            :class="selectedBilling === planType.id ? 'bg-accent' : ''"
            @click="changePlanType(planType.id)"
          >
            <q-icon
              v-if="i !== 0"
              size="sm"
              name="icon-sale-02"
              color="secondary"
            />
            <span
              class="text-body2 font-medium"
              :class="i !== 0 ? 'q-pl-sm' : ''"
              >{{ planType.label }}</span
            >
          </q-btn>
          <q-separator
            vertical
            v-if="i < Object.keys(subscription_BillingTypes).length - 1"
          />
        </template>
      </q-btn-group>
    </div>

    <div :class="nextButton ? 'q-pt-md' : ''">
      <q-list>
        <div class="row" :class="`q-col-gutter-${cardsGutter}`">
          <div
            v-for="talent in subscription_Talents"
            :key="talent.id"
            class="col-12 col-md-6"
          >
            <card-item
              v-ripple="
                checkIfPlanSelected(talent.value, selectedBilling)
                  ? false
                  : { color: 'secondary' }
              "
              :class="
                checkIfPlanSelected(talent.value, selectedBilling)
                  ? 'badge-bg-active badge-border-active'
                  : talent.value === selectedTalent
                  ? 'border-secondary bg-accent hover-border-secondary hover-bg-accent'
                  : 'hover-border-secondary hover-bg-accent'
              "
              class="q-py-sm cursor-pointer border-add hover-border-secondary"
              @click="
                checkIfPlanSelected(talent.value, selectedBilling)
                  ? void 0
                  : (selectedTalent = talent.value)
              "
            >
              <template v-slot:avatar>
                <q-radio
                  v-model="selectedTalent"
                  :val="talent.value"
                  color="secondary"
                  class="negative-margin-1"
                  :unchecked-icon="
                    checkIfPlanSelected(talent.value, selectedBilling)
                      ? 'icon-check-circle-broken badge-text-active'
                      : undefined
                  "
                  :class="
                    checkIfPlanSelected(talent.value, selectedBilling)
                      ? 'cursor-pointer no-pointer-events'
                      : ''
                  "
                  :rules="[(val) => (val && val !== '' && val !== null) || '']"
                />
              </template>
              <template v-slot:title>
                <div class="letter-space-normal">
                  <template v-if="talent.value > 0">
                    {{ subscription.label }} {{ talent.value }}
                  </template>
                  <template v-else>Custom Plan</template>
                </div>
              </template>
              <template v-slot:description>
                <div
                  class="q-py-xs text-body2 font-medium"
                  :class="
                    checkIfPlanSelected(talent.value, selectedBilling)
                      ? 'badge-text-active'
                      : talent.value === selectedTalent
                      ? 'text-secondary'
                      : 'text-primary'
                  "
                  v-bind="
                    (priceInfo = planPriceCalculator(
                      talent.value,
                      selectedBilling
                    ))
                  "
                >
                  <span> {{ priceInfo.label }} </span>
                  {{
                    subscription_BillingTypes[selectedBilling]?.breakdown_label
                  }}
                </div>
                <div class="text-body2 text-primary">
                  {{ subscription.description_1 }}
                  {{
                    talent.value === -1 ? 'custom' : numberToWords(talent.value)
                  }}
                  {{
                    talent.value > 1 || talent.value === -1 ? 'users' : 'user'
                  }}.
                </div>
              </template>
            </card-item>
          </div>
          <q-item
            class="q-pa-none q-pt-md col-12 items-center justify-end"
            v-if="nextButton && selectedTalent && selectedBilling"
          >
            <q-btn
              no-caps
              unelevated
              color="primary"
              class="rounded slide-hover button-md text-body1 font-medium"
              @click="nextButtonClicked"
            >
              Request Change
              <q-icon
                size="20px"
                name="icon-arrow-narrow-right"
                class="q-pl-xs"
              />
            </q-btn>
          </q-item>
        </div>
      </q-list>
    </div>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { formatMoney, numberToWords } from 'src/assets/scripts/functions';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import fnStore from 'src/stores/fn.store';

export default defineComponent({
  name: 'SubscriptionPlans',

  emits: ['updated', 'talentChanged', 'billingChanged', 'nextBtnClick'],

  props: {
    nextButton: {
      type: Boolean,
      default: false,
    },
    planTypes: {
      type: Boolean,
      default: true,
    },
    cardsGutter: {
      type: String,
      default: 'md',
    },
  },

  data() {
    return {
      selectedTalent: 0,
      selectedBilling: 1,
      numberToWords,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, [
      'subscription',
      'subscription_Talents',
      'subscription_BillingTypes',
    ]),

    orgSubscription() {
      return !fnStore.getAuth() ? {} : this.selectedOrg?.subscription || {};
    },
  },

  watch: {
    selectedTalent: {
      async handler(val) {
        this.talentChanged(val);
        this.updated();
      },
    },

    selectedBilling: {
      async handler(val) {
        this.selectedTalent = 0;
        this.billingChanged(val);
        this.updated();
      },
    },
  },

  methods: {
    ...mapActions(useGrowmodo, ['planPriceCalculator']),

    updated() {
      this.$emit('updated', {
        talent: this.selectedTalent,
        billing: this.selectedBilling,
      });
    },

    talentChanged(val) {
      this.$emit('talentChanged', val || this.selectedTalent);
    },

    billingChanged(val) {
      this.$emit('billingChanged', val || this.selectedBilling);
    },

    checkIfPlanSelected(subscription_talent_id, subscription_billing_id) {
      return (
        this.orgSubscription.subscription_billing_id ==
          subscription_billing_id &&
        this.orgSubscription.subscription_talent_id == subscription_talent_id
      );
    },

    changePlanType(planType) {
      // Set New Plan Type
      this.selectedBilling = planType;
    },

    formatMoney(money, minimumFractionDigits = 0) {
      return formatMoney(money, { minimumFractionDigits });
    },

    nextButtonClicked() {
      this.$emit('nextBtnClick', {
        selectedTalent: this.selectedTalent,
        selectedBilling: this.selectedBilling,
      });
    },
  },
});
</script>
