<template>
  <div class="q-gutter-y-lg">
    <q-card flat bordered class="rounded">
      <q-card-section class="card-padding-title-xs">
        <q-toolbar class="q-pa-none">
          <q-toolbar-title class="text-h5 text-primary-700 font-medium">
            Profile Information
          </q-toolbar-title>

          <div class="q-gutter-sm">
            <DynamicButton
              v-if="!editing"
              outline
              alwaysShowIcon
              iconSize="xs"
              label="Edit Info"
              icon="icon-pencil-02"
              @click="editing = true"
              class="button-gm-outline-dark"
            />

            <DynamicButton
              v-if="editing"
              outline
              :flat="$q.screen.lt.xs || $q.screen.xs"
              iconSize="xs"
              label="Cancel"
              icon="icon-x-close"
              class="button-gm-outline-dark"
              @click="cancelEditing"
            />

            <DynamicButton
              v-if="editing"
              alwaysShowIcon
              iconSize="xs"
              label="Save Changes"
              icon="icon-save-01"
              @click="saveAccountInfo"
            />
          </div>
        </q-toolbar>
      </q-card-section>
      <q-separator />
      <q-card-section
        class="q-col-gutter-lg text-body1 card-padding"
        :style="editing ? 'max-width: 900px' : ''"
      >
        <div
          class="row full-wdith items-center justify-start"
          :class="{ ['q-col-gutter-md']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Full Name<span v-if="editing" class="text-red">*</span>
          </div>
          <div v-if="editing" class="col-12 col-sm-8 col-md-9">
            <div class="row q-col-gutter-md items-center justify-start">
              <div class="col-12 col-md-6">
                <q-input
                  outlined
                  placeholder="First Name"
                  v-model="account.firstname"
                  @change="changes_made = true"
                />
              </div>
              <div class="col-12 col-md-6">
                <q-input
                  outlined
                  placeholder="Last Name"
                  v-model="account.lastname"
                  @change="changes_made = true"
                />
              </div>
            </div>
          </div>
          <div v-else class="col-12 col-sm-8 col-md-9 letter-space-normal">
            <div>{{ user.firstname }} {{ user.lastname }}</div>
          </div>
        </div>
        <div
          v-if="!isOrgOwner()"
          class="row full-wdith items-center justify-start"
          :class="{ ['q-col-gutter-md']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Username<span v-if="editing" class="text-red">*</span>
          </div>
          <div v-if="editing" class="col-12 col-sm-8 col-md-9">
            <q-input
              outlined
              placeholder="Username"
              v-model="account.username"
              @change="changes_made = true"
            />
          </div>
          <div v-else class="col-12 col-sm-8 col-md-9 letter-space-normal">
            <div>{{ user.username }}</div>
          </div>
        </div>
        <div
          class="row full-wdith items-center justify-start"
          :class="{ ['q-col-gutter-sm']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Email Address<span v-if="editing" class="text-red">*</span>
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <q-input
              v-if="editing"
              outlined
              placeholder="example@email.com"
              v-model="account.email"
              @change="changes_made = true"
            />
            <div v-else class="letter-space-normal">
              <a
                :href="'mailto:' + user.email"
                target="_blank"
                class="text-primary"
              >
                {{ user.email }}
              </a>
            </div>
          </div>
        </div>
        <div
          v-if="
            0 && (editing || (user.phone_country_code && user.phone_number))
          "
          class="row full-wdith items-center justify-start"
          :class="{ ['q-col-gutter-sm']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Phone Number<span v-if="editing" class="text-red"></span>
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <InputPhoneNumber
              v-if="editing"
              @areaCodeChanged="
                (e) => {
                  account.phone_country_code = e;
                  if (e != user.phone_country_code) changes_made = true;
                }
              "
              @numberChanged="
                (e) => {
                  account.phone_number = e;
                  if (e != user.phone_number) changes_made = true;
                }
              "
              :areaCode="account.phone_country_code"
              :phoneNumber="account.phone_number"
            />
            <div v-else class="letter-space-normal">
              <external-link
                labelClass="text-primary"
                :url="`tel:+${user.phone_country_code}${user.phone_number}`"
                :label="`+${user.phone_country_code} ${user.phone_number}`"
              />
            </div>
          </div>
        </div>
        <div
          v-if="!editing"
          class="row full-wdith items-center justify-start"
          :class="{ ['q-col-gutter-sm']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Company
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <div class="letter-space-normal">{{ selectedOrg.title }}</div>
          </div>
        </div>
        <div
          v-if="!editing || isOrgOwner()"
          class="row full-wdith items-center justify-start"
          :class="{ ['q-col-gutter-sm']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Role<span v-if="editing" class="text-red">*</span>
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <template v-if="editing && isOrgOwner()">
              <SelectRoles
                v-if="isOrgOwner()"
                v-model="account.title"
                :initModel="user.title"
                @updated="changes_made = true"
              />
              <SelectUserRole
                v-else
                v-model="account.role"
                :initModel="user.role"
                @updated="changes_made = true"
              />
            </template>
            <div v-else-if="!editing" class="letter-space-normal">
              <template v-if="isOrgOwner()">
                {{ user.title }}
              </template>
              <template v-else>
                {{
                  userInvitedRoles[user.role]?.label || user.role || 'Unknown'
                }}
              </template>
            </div>
          </div>
        </div>
        <div
          v-if="!editing"
          class="row full-wdith justify-start"
          :class="{ ['q-col-gutter-sm']: editing }"
        >
          <div
            class="col-12 col-sm-4 col-md-3 font-medium"
            :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
          >
            Password
          </div>
          <div class="col-12 col-sm-8 col-md-9">
            <div class="letter-space-normal">************</div>
          </div>
        </div>
      </q-card-section>
    </q-card>

    <form
      autocorrect="off"
      autocapitalize="off"
      autocomplete="off"
      spellcheck="false"
    >
      <q-card v-if="editing" flat bordered class="rounded">
        <q-card-section class="card-padding-xs">
          <q-toolbar class="q-pa-none" style="min-height: unset">
            <q-toolbar-title class="text-h5 text-primary-700 font-medium">
              Account Security
            </q-toolbar-title>

            <div class="q-gutter-sm">
              <DynamicButton
                iconSize="xs"
                label="Update Password"
                icon="icon-save-01"
                @click="updatePwd"
                color="primary"
              />
            </div>
          </q-toolbar>
        </q-card-section>
        <q-separator />
        <q-card-section
          class="q-col-gutter-lg card-padding-xs"
          :style="editing ? 'max-width: 900px' : ''"
        >
          <div
            class="row full-width items-center justify-start"
            :class="{ ['q-col-gutter-sm']: editing }"
          >
            <div
              class="col-12 col-sm-4 col-md-3 font-medium"
              :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
            >
              Old Password<span v-if="editing" class="text-red">*</span>
            </div>
            <div class="col-12 col-sm-8 col-md-9">
              <InputPassword
                v-if="editing"
                v-model="account.password_old"
                placeholder="Enter Your Current Password"
                tabindex="1"
              />
              <div v-else class="letter-space-normal">*******</div>
            </div>
          </div>
          <div
            class="row full-width items-center justify-start"
            :class="{ ['q-col-gutter-sm']: editing }"
          >
            <div
              class="col-12 col-sm-4 col-md-3 font-medium"
              :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
            >
              New Password<span v-if="editing" class="text-red">*</span>
            </div>
            <div class="col-12 col-sm-8 col-md-9">
              <InputPassword
                v-if="editing"
                v-model="account.password_new"
                tabindex="2"
              />
              <div v-else class="letter-space-normal">*******</div>
            </div>
          </div>
          <div
            class="row full-width items-center justify-start"
            :class="{ ['q-col-gutter-sm']: editing }"
          >
            <div
              class="col-12 col-sm-4 col-md-3 font-medium"
              :class="{ [`text-primary-${editing ? '700' : '500'}`]: true }"
            >
              Confirm New Password<span v-if="editing" class="text-red">*</span>
            </div>
            <div class="col-12 col-sm-8 col-md-9">
              <InputPassword
                v-if="editing"
                v-model="account.password_conf"
                tabindex="3"
              />
              <div v-else class="letter-space-normal">*******</div>
            </div>
          </div>
        </q-card-section>
      </q-card>
    </form>

    <!-- Dialog Unsaved Edit -->
    <q-dialog v-model="unsaved">
      <dynamic-dialog-confirmation
        v-bind="$root.messagesDialogs?.editingUnsaved"
        @okay="editing = false"
      />
    </q-dialog>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import {
  extractErrorMessageString,
  objectCopy,
} from 'src/assets/scripts/functions';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import InputPhoneNumber from 'src/components/Helpers/InputPhoneNumber.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import SelectRoles from 'src/components/Helpers/SelectRoles.vue';
import SelectUserRole from 'src/components/Helpers/SelectUserRole.vue';

export default defineComponent({
  name: 'AccountProfile',

  components: {
    DynamicButton,
    InputPassword,
    InputPhoneNumber,
    SelectRoles,
    SelectUserRole,
  },

  data() {
    return {
      unsaved: false,
      editing: false,
      account: {},
      changes_made: false,
    };
  },

  computed: {
    ...mapState(useUser, ['user', 'selectedOrg']),
    ...mapState(useGrowmodo, ['userInvitedRoles']),
  },

  watch: {
    editing: {
      async handler(val) {
        if (val) {
          // Editing
          // Update the editing value
          this.account = objectCopy(this.user || {});
        } else {
          // Viewing
          // Reset company info
          this.account = {};
          this.changes_made = false;
        }
      },
    },
  },

  methods: {
    ...mapActions(useUser, ['updateAccount', 'updatePassword', 'isOrgOwner']),

    async saveAccountInfo() {
      if (!this.changes_made) {
        this.editing = false;
        // this.$q.notify({
        //   icon: 'icon-check-circle-broken',
        //   message: 'No changes has been made',
        // });
        return;
      }
      const _ = this.account || {};
      const payload = {
        username: _.username,
        firstname: _.firstname,
        lastname: _.lastname,
        email: _.email,
        phone_number: _.phone_number,
        phone_country_code: _.phone_country_code,
      };
      if (this.isOrgOwner()) {
        payload.title = _.title;
      } else {
        // payload.role = _.role;
      }
      const response = await this.updateAccount(payload);
      if (response.success) {
        this.editing = false;
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: 'Your changes have been successfully saved',
        });
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Failed to update profile',
        });
      }
    },

    async updatePwd() {
      let status = {};
      switch (true) {
        case !this.account.password_old:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'Please provide your old password to verify';
          break;

        case !this.account.password_new:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'Please provide your new password';
          break;

        case !this.account.password_conf:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'Please confirm your new password';
          break;

        case this.account.password_new !== this.account.password_conf:
          status.icon = 'icon-alert-triangle';
          status.iconColor = 'negative';
          status.message = 'The password you entered did not match';
          break;

        default:
          status = true;
          break;
      }
      if (status === true) {
        const payload = {
          old_password: this.account.password_old,
          new_password: this.account.password_new,
        };
        const response = await this.updatePassword(payload);
        if (response.success) {
          this.account.password_old = '';
          this.account.password_new = '';
          this.account.password_conf = '';
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: 'Your password have been successfully updated',
          });
        } else {
          const message = extractErrorMessageString(response?.message);
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: message || 'Error updating password',
          });
        }
      } else if (status.message) this.$q.notify(status);
    },

    cancelEditing() {
      if (this.changes_made) {
        this.unsaved = true;
      } else {
        this.editing = false;
      }
    },
  },
});
</script>
