<template>
  <div class="q-gutter-y-lg">
    <q-card flat bordered class="rounded">
      <q-card-section class="card-padding-title-xs">
        <q-toolbar class="q-pa-none">
          <q-toolbar-title class="text-h5 text-primary-700 font-medium">
            Your Current Plan
          </q-toolbar-title>

          <div
            v-if="0 && (isOrgOwner() || isOrgAdmin() || isOrgBiller())"
            class="q-gutter-sm"
          >
            <DynamicButton
              v-if="!editing"
              outline
              alwaysShowIcon
              iconSize="xs"
              label="Edit Subscription"
              icon="icon-pencil-02"
              @click="editing = true"
              class="button-gm-outline-dark"
            />

            <DynamicButton
              v-if="editing && selectedOrgIsPaused"
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Resume Subscription"
              icon="icon-arrow-circle-broken-up"
              color="secondary"
              class="hover-dark"
              @click="resumeAccount"
            />

            <DynamicButton
              v-else-if="editing"
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Cancel Subscription"
              icon="icon-arrow-circle-broken-down"
              color="negative"
              @click="startOffBoarding = true"
            />

            <DynamicButton
              v-if="editing"
              :flat="$q.screen.lt.xs || $q.screen.xs"
              label="Cancel Changes"
              icon="icon-x-close"
              @click="editing = false"
            />
          </div>
        </q-toolbar>
      </q-card-section>
      <q-separator />
      <q-card-section class="card-padding">
        <div class="row items-center q-col-gutter-md q-py-sm">
          <div class="col-grow col-md-4 text-secondary">
            <div
              class="text-h5 font-medium"
              :class="{
                ['text-primary-700']: selectedOrgIsPaused,
                ['text-secondary']: !selectedOrgIsPaused,
              }"
            >
              <template v-if="selectedOrgIsMaintenance">
                {{ maintenance.label }} Plan
              </template>
              <template v-else>
                {{ subscription.label }}
                {{ talentCount.value }} -
                {{
                  subscription_BillingTypes[
                    orgsubscription.subscription_billing_id
                  ]?.billed_label
                }}
              </template>
            </div>
            <div class="text-body1 text-primary-700 letter-space-normal">
              Next Billing on
              <span class="font-medium">{{ nextBillingLabel }}</span>
            </div>
          </div>
          <div class="col-grow col-md-8">
            <div class="row q-col-gutter-sm justify-end">
              <div>
                <DynamicButton
                  flat
                  outline
                  format="sm"
                  alwaysShowIcon
                  iconSize="xs"
                  label="Cancel"
                  icon="icon-cancel"
                  color="negative"
                  class="q-px-sm"
                  labelClass="text-body1"
                  @click="startOffBoarding = true"
                />
              </div>
              <div>
                <DynamicButton
                  v-if="selectedOrgIsPaused"
                  outline
                  format="sm"
                  alwaysShowIcon
                  iconSize="xs"
                  label="Resume"
                  icon="icon-play"
                  class="button-gm-outline-dark q-px-sm"
                  labelClass="text-body1"
                  @click="resumeAccount"
                />
                <DynamicButton
                  v-else
                  outline
                  format="sm"
                  alwaysShowIcon
                  iconSize="xs"
                  label="Pause"
                  icon="icon-pause-circle"
                  class="button-gm-outline-dark q-px-sm"
                  labelClass="text-body1"
                  @click="
                    startOffBoarding = true;
                    setOffBoardingStep = selectedOrgIsMaintenance
                      ? 'pause_2'
                      : 'pause_1';
                  "
                />
              </div>
              <div>
                <DynamicButton
                  format="sm"
                  alwaysShowIcon
                  iconSize="xs"
                  label="Discount"
                  color="secondary"
                  icon="icon-percent-03"
                  class="hover-bg-secondary2 button-gm-highlight q-px-sm"
                  labelClass="text-body1"
                  @click="discountDialog = true"
                />
              </div>
            </div>
          </div>
        </div>
      </q-card-section>
    </q-card>

    <q-card v-if="editing" flat bordered class="rounded">
      <q-card-section :class="$q.screen.gt.sm ? 'q-pa-lg' : 'q-pa-md'">
        <SubscriptionPlans
          :nextButton="true"
          @updated="planUpdated"
          @nextBtnClick="nextBtnClick"
        />
      </q-card-section>
    </q-card>

    <!-- Offboarding -->
    <q-dialog v-model="startOffBoarding">
      <OffBoarding
        class="rounded"
        style="width: 1080px"
        :style="`max-width: ${
          offboardingStep == 'pause_done'
            ? '560px'
            : offboardingStep == 'maintenance_done'
            ? '800px'
            : '80vw'
        }`"
        @stepUpdated="
          (val) => {
            offboardingStep = val;
          }
        "
        :setStep="setOffBoardingStep"
        @exit="
          () => {
            setOffBoardingStep = 'init';
            startOffBoarding = false;
          }
        "
      />
    </q-dialog>

    <!-- Subscription Discount -->
    <q-dialog
      v-model="discountDialog"
      transition-show="scale"
      transition-hide="scale"
      :transition-duration="300"
    >
      <Dialog_SubscriptionDiscount
        noclose
        style="width: 700px"
        :style="
          $q.screen.xs && $q.screen.lt.sm
            ? 'max-width: 95vw;'
            : 'max-width: 80vw'
        "
        @requestChanges="discountDialog = false"
      />
    </q-dialog>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import { date } from 'quasar';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import SubscriptionPlans from 'src/components/Helpers/Subscription/SubscriptionPlans.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import useGrowmodo from 'stores/growmodo';
import OffBoarding from 'src/components/Dialogs/OffBoarding.vue';
import {
  numberToWords,
  extractErrorMessageString,
} from 'src/assets/scripts/functions';
import fnStore from 'src/stores/fn.store';
import Dialog_SubscriptionDiscount from 'src/components/Dialogs/SubscriptionDiscount.vue';

export default defineComponent({
  name: 'SubscriptionInformation',

  components: {
    DynamicButton,
    SubscriptionPlans,
    OffBoarding,
    Dialog_SubscriptionDiscount,
  },

  data() {
    return {
      editing: false,
      numberToWords,
      startOffBoarding: false,
      setOffBoardingStep: 'init',
      offboardingStep: '',
      discountDialog: false,
    };
  },

  computed: {
    ...mapState(useUser, [
      'organizations',
      'selectedOrg',
      'selectedOrgIsMaintenance',
      'selectedOrgIsPaused',
    ]),
    ...mapState(useGrowmodo, [
      'subscription',
      'subscription_BillingTypes',
      'subscription_Talents',
      'maintenance',
    ]),

    orgsubscription() {
      return !fnStore.getAuth() ? {} : this.selectedOrg?.subscriptions;
    },

    talentCount() {
      return (
        this.subscription_Talents[
          this.orgsubscription.subscription_talent_id
        ] || {}
      );
    },

    nextBilling() {
      return date.addToDate(this.orgsubscription.subscription_end, { days: 1 });
    },

    nextBillingLabel() {
      return date.formatDate(this.nextBilling, 'Do MMMM YYYY');
    },
  },

  methods: {
    ...mapActions(useUser, [
      'isOrgOwner',
      'isOrgAdmin',
      'isOrgBiller',
      'resumeSubscription',
    ]),

    nextBtnClick() {
      this.editing = false;
      this.$root.dialogRequestUpgrade = true;
    },

    planUpdated(planinfo) {
      if (process.env.DEV_MODE) console.log('Plan Changed:', planinfo);
    },

    async resumeAccount() {
      const response = await this.resumeSubscription({});
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.formStep = 'pause_done';
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: message || 'Subscription has been resumed',
        });
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Error while processing your request',
        });
      }
    },
  },
});
</script>
