<template>
  <q-card flat class="rounded">
    <q-card-section class="card-padding-title-xs">
      <q-toolbar class="text-primary q-pa-none">
        <q-toolbar-title class="text-h6 font-medium">
          {{ newUser ? 'Invite User' : 'Users Information' }}
          <q-badge
            v-if="selectedOrgUsers?.[uid]?.status === 'pending'"
            class="q-ml-md badge badge-warning"
          >
            Invitation Pending
          </q-badge>
        </q-toolbar-title>

        <div class="q-gutter-sm">
          <DynamicButton
            v-close-popup
            outline
            :flat="$q.screen.lt.xs || $q.screen.xs"
            iconSize="xs"
            label="Cancel"
            class="button-gm-outline-dark"
            icon="icon-x-close"
          />
          <DynamicButton
            alwaysShowIcon
            iconSize="xs"
            :label="newUser ? 'Invite User' : 'Save Changes'"
            :icon="newUser ? 'icon-user-plus-01' : 'icon-save-01'"
            @click="saveUserInfo"
          />
        </div>
      </q-toolbar>
    </q-card-section>

    <q-separator />

    <q-card-section
      class="card-padding"
      :class="$q.screen.gt.sm ? 'q-col-gutter-lg' : 'q-col-gutter-md'"
    >
      <div class="row items-center justify-start q-col-gutter-sm">
        <div class="col-12 col-sm-4 col-md-3 font-medium text-primary-600">
          Username<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            dense
            outlined
            v-model="account.username"
            placeholder="username"
            tabindex="1"
          />
        </div>
      </div>
      <div class="row items-center justify-start col-6 q-col-gutter-sm">
        <div class="col-12 col-sm-4 col-md-3 font-medium text-primary-600">
          Password<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <div class="row q-col-gutter-md items-center justify-start">
            <div class="col-12 col-md-6">
              <InputPassword v-model="account.password" tabindex="2" />
            </div>
          </div>
        </div>
      </div>
      <div class="row items-center justify-start q-col-gutter-md">
        <div class="col-12 col-sm-4 col-md-3 font-medium text-primary-600">
          Full Name<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <div class="row q-col-gutter-md items-center justify-start">
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="First Name"
                v-model="account.firstname"
                tabindex="3"
              />
            </div>
            <div class="col-12 col-md-6">
              <q-input
                dense
                outlined
                placeholder="Last Name"
                v-model="account.lastname"
                tabindex="4"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="row items-center justify-start q-col-gutter-sm">
        <div class="col-12 col-sm-4 col-md-3 font-medium text-primary-600">
          User Role<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-6 col-md-9">
          <div class="row q-col-gutter-md items-center justify-start">
            <div class="col-12 col-md-6">
              <SelectUserRole
                v-model="account.role"
                :initModel="account.role"
                tabindex="5"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="row items-center justify-start q-col-gutter-sm">
        <div class="col-12 col-sm-4 col-md-3 font-medium text-primary-600">
          Email Address<span class="text-red">*</span>
        </div>
        <div class="col-12 col-sm-8 col-md-9">
          <q-input
            dense
            outlined
            placeholder="example@email.com"
            v-model="account.email"
            tabindex="6"
          />
        </div>
      </div>
    </q-card-section>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import DynamicButton from 'src/components/Helpers/DynamicButton.vue';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import { mapActions, mapState } from 'pinia';
import useUser from 'stores/user';
import {
  checkIfObject,
  extractErrorMessageString,
  objectCopy,
} from 'src/assets/scripts/functions';
import SelectUserRole from 'src/components/Helpers/SelectUserRole.vue';

export default defineComponent({
  name: 'Add_Edit_User',

  components: { DynamicButton, InputPassword, SelectUserRole },

  emits: ['added', 'updated', 'error', 'close'],

  props: {
    uid: {
      type: [String, Number],
      default: null,
    },
  },

  data() {
    return {
      userInfo: null,
      account: {},
      newUser: true,
    };
  },

  computed: {
    ...mapState(useUser, ['selectedOrgUsers']),
  },

  mounted() {
    this.userInfo = this.selectedOrgUsers[this.uid];
    if (this.userInfo) {
      this.account = objectCopy(this.userInfo);
      this.newUser = false;
    }
  },

  methods: {
    ...mapActions(useUser, ['addOrgUser', 'updateOrgUser']),

    async saveUserInfo() {
      if (
        !this.account ||
        !(checkIfObject(this.account) && Object.values(this.account).join(''))
      )
        return;
      if (this.newUser) {
        // Register New User
        const _ = this.account;
        const payload = {
          username: _.username,
          password: _.password,
          firstname: _.firstname,
          lastname: _.lastname,
          role: _.role,
          email: _.email,
        };
        const add_response = await this.addOrgUser(payload);
        const add_message = extractErrorMessageString(add_response?.message);
        if (add_response.success) {
          this.$emit('added', add_response.data?.user?.id);
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: 'Your invitation has been sent',
          });
        } else {
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: add_message || 'Invitation failed',
          });
        }
      } else {
        // Update Existing User
        const _ = this.account;
        const payload = {
          username: _.username,
          password: _.password,
          firstname: _.firstname,
          lastname: _.lastname,
          role: _.role,
          email: _.email,
        };
        const edit_response = await this.updateOrgUser(this.uid, payload);
        const edit_message = extractErrorMessageString(edit_response?.message);
        if (edit_response.success) {
          this.$emit('updated', this.uid);
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: 'Your changes have been successfully saved',
          });
        } else {
          this.$q.notify({
            icon: 'icon-alert-triangle',
            iconColor: 'negative',
            message: edit_message || 'An error occured while updating the user',
          });
        }
      }
    },
  },

  beforeUnmount() {
    this.$emit('close');
  },
});
</script>
